@import url("https://cdnjs.cloudflare.com/ajax/libs/meyer-reset/2.0/reset.min.css")
@import url("https://fonts.googleapis.com/css?family=Noto+Sans+SC:500,700,400")
/* The following line is used to measure usage of this code. You can remove it if you want. */
@import url("https://px.animaapp.com/6189dc86cadb55141aee6bfa.6189dc881ebc77dda1e38f67.eGx8qrq.rsp.png")





.screen a
  display: contents
  text-decoration: none

.container-center-horizontal
  display: flex
  flex-direction: row
  justify-content: center
  pointer-events: none
  width: 100%

.container-center-horizontal > *
  flex-shrink: 0
  pointer-events: auto

.valign-text-middle
  display: flex
  flex-direction: column
  justify-content: center

input:focus
  outline: none

*
  box-sizing: border-box